import './App.css';
import { BrowserRouter as Router, Route, Routes} from 'react-router-dom';
import Home from './pages/homepage/Home';

function App() {
  return (
    <div>
      <Router>
          <Routes>
            <Route exact path='/' element={<Home/>}></Route>
          </Routes>
      </Router>
    </div>
  );
}

export default App;
